.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
p {
  color: white;
  text-shadow: 2px 2px 4px #050505;
}
p:hover {
  color: aqua;
  cursor: pointer;
}
.header-bar {
  font-size: larger;
  text-align: center;
  color: #21a8ce;
  background-color: #282c34;
}
header {
  display: block;
  align-items: center;
}
.about-link {
  color: aqua;
}
.about-link:hover {
  transform: scale(120%);
}
@import url('https://fonts.googleapis.com/css?family=Source+Code+Pro');
body {
  /* display: inline-block; */
  max-width: 1650px;
  max-height: 430px;
}
.blockQuote {
  font-family: Cambria, Cochin, Georgia, Times, 'Times New Roman', serif;
  color: rgb(129, 127, 255);
}
.wrapper {
  display: block;
  grid-column-start: 3;
  grid-column-end: 4;
  grid-row-start: 1;
  grid-row-end: 2;
  border-left: 10px black;
  border-top: 10px black;
}
div.deal-links-container {
  position: relative;
  grid-column-start: 1;
  grid-column-end: 2;
  grid-row-start: 2;
  grid-row-end: 3;
  size: 480px 30px;
}
.gro-container {
  display: grid;
  max-width: 1650px;
  max-height: 430px;
  grid-template-columns: 400px 400px 400px 400px;
  grid-template-columns: 1fr 1fr, minmax(10px, 1fr) 3fr, repeat(5, 1fr), 50px auto 100px 1fr;
  grid-template-rows: 50px 40px 100px 100px 100px 50px;
  grid-template-rows: min-content 1fr min-content, 100px 1fr max-content;
  grid-gap: 10px;
}
.button-div {
  display: block;
  grid-column-start: 2;
  grid-column-end: 3;
  grid-row-start: 1;
  grid-row-end: 2;
  max-width: 80%;
  max-height: 80%;
}
#gro-button {
  display: block;
  max-width: 50%;
  max-height: 50%;
  font-weight: bolder;
}
#gro-button:hover {
  background-color: rgb(216, 197, 197);
  color: crimson;
  cursor: pointer;
}
@keyframes fadeIn {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
#dealInfo {
  font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
  animation: fadeIn 6s;
  color: black;
}
#productsInfo {
  font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
  animation: fadeIn 10s;
  color: black;
}
#prod-div {
  display: block;
  grid-column-start: 2;
  grid-column-end: 3;
  grid-row-start: 2;
  grid-row-end: 6;
}
#product-id-output {
  font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
  animation: fadeIn 10s;
  color: black;
}
.prod-id-wrapper {
  grid-column-start: 3;
  grid-column-end: 4;
  grid-row-start: 2;
  grid-row-end: 6;
}
