/* text animation */

@keyframes animated-text {
  from {
    width: 0;
  }
  to {
    width: 100px;
  }
}
.typing-demo {
  width: 55ch;
  animation: typing 2s steps(72), step-end blink 0.5s infinite alternate;
  white-space: nowrap;
  overflow: hidden;
  font-family: monospace;
  font-size: 1.5em;
  color: rgb(8, 8, 8);
}

@keyframes typing {
  from {
    width: 0;
  }
}

@keyframes blink {
  50% {
    border-color: transparent;
  }
}
p.title {
  font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
  color: rgb(14, 13, 13);
  grid-column-start: 4;
  grid-column-end: 5;
  grid-row-start: 1;
  grid-row-end: 1;
}

